<template>
  <div id="right-chart-3">
    <div class="header-3">近七天使用情况</div>
    <div id="body-3">

    </div>

  </div>
</template>
<script>
import * as echarts from "echarts";
export default {
  data() {},
  mounted() {
    var chartDom = document.getElementById("body-3");
    var myChart = echarts.init(chartDom);
    var option;

    option = {
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "shadow",
        },
        formatter: '{a0}:{c0}%'
      },
      /* title: {
        text: "近七天使用情况",

        textStyle: {
          color: "#df9c00",

        },
      }, */
      textStyle: {
        color: "rgb(131 ,139,139)",
        fontStyle: "normal",
        fontWeight: "normal",
        fontFamily: "sans-serif",
        fontSize: 18, //字体大小
        lineHeight: 18,
      },
      grid: {
        top: "24%",
        left: "3%",
        right: "10%",
        bottom: "3%",
        containLabel: true,
      },
      xAxis: {
        type: "category",
       // name: "t",
        nameLocation: "end",
        nameTextStyle: {
          color:'rgba(255, 255, 255, 0.4)',
          fontSize: "15px",
        },
        data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
        axisLine: {
          symbol: ["none", "none"], //箭头一端没效果,一端箭头,改成两端都不带箭头
         // symbolOffset: [0, 9], //箭头段移动8个像素
          lineStyle: {
           color:'rgba(255, 255, 255, 0.4)'
          },
        },
      },
      yAxis: {
        type: "value",
        //name: "%00",
       /*  nameLocation: "strat",
        nameTextStyle: {
          color:'rgba(255, 255, 255, 0.4)',
          fontSize: "10px",

          padding: [0, -28, 32, 0],
        }, */
        axisLine: {
          show: false,
          //symbol: ["none", "none"], //箭头一端没效果,一端箭头
         // symbolOffset: [0, 9], //箭头段移动8个像素
         /*  lineStyle: {
            color:'rgba(255, 255, 255, 0.4)'
          }, */
          /* axisTick: {
            alignWithLabel: true,
          }, */
        },
        axisTick:{
            show:false,
        },
        splitLine: {
          show: true,
          lineStyle: {
            type: "dashed",
            color: "rgba(255, 255, 255, 0.4)",
          },
        },
      },
      series: [
        {
          name:"所占百分比",
         data: this.$store.state.RightChart3Data,         
          type: "line",
          smooth: true,
            lineStyle: { // 设置线条的style等
              normal: {
              color: '#3AD0FF', // 折线线条颜色
             },
           },
           itemStyle:{
             normal:{
               color:'#3AD0FF',
              /*  symbolSize:20, */
             },
           },
        },
      ],
    };

    option && myChart.setOption(option);
  },

  name: "RightChart2",
};
