<template>
    <div id="container" style="height: 100%;width: 100%"></div>
</template>
<script>
import * as echarts from 'echarts';
import 'echarts-gl'
export default {
  mounted () {
    this.init()
  },
  methods:{
    init(){
      var ROOT_PATH =
        // 'https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples';
        './';

      var dom = document.getElementById("container");
      var myChart = echarts.init(dom);
      var app = {};

      var option;



      $.getJSON(ROOT_PATH + '/data-gl/asset/data/flights.json', function (data) {
        var airports = data.airports.map(function (item) {
          return {
            coord: [item[3], item[4]]
          };
        });

        console.log(airports);
        function getAirportCoord(idx) {
          return [data.airports[idx][3], data.airports[idx][4]];
        }
        // Route: [airlineIndex, sourceAirportIndex, destinationAirportIndex]
        var routesGroupByAirline = {};
        data.routes.forEach(function (route) {
          var airline = data.airlines[route[0]];
          var airlineName = airline[0];
          if (!routesGroupByAirline[airlineName]) {
            routesGroupByAirline[airlineName] = [];
          }
          routesGroupByAirline[airlineName].push(route);
        });

        var pointsData = [];
        let computes = [];
        // 计算节点  64 * 4 * 12 + 12
        //整个一圈
        let lat = -180, lon = 60;
        for (let i = 0; i < 12; i++) {
          //减纬度
          for (let j = 0; j < 4; j++) {
            for (let k = 0; k < 64; k++) {
              let x = (k % 16);
              let y = parseInt(k / 16);
              // {value:[x * 2.4 + j * 45 + lat, y * 0.8 + lon],itemStyle:{color:Math.random() > 0.9 ? "rgb(200,0,0)" :"rgb(50,50,150)"}}
              pointsData.push({ value: [x * 2.4 + j * 45 + lat, y * 0.8 + lon], itemStyle: { color: Math.random() > 0.9 ? "rgb(200,0,0)" : "rgb(50,50,150)" } });
              if (Math.random() > 0.95) {
                computes.push([x * 2.4 + j * 45 + lat, y * 0.8 + lon]);
              }
            }
          }
          lon -= 5.2;
        }
        lat = 0;
        lon = 60;
        for (let i = 0; i < 12; i++) {
          //减纬度
          for (let j = 0; j < 4; j++) {
            for (let k = 0; k < 64; k++) {
              let x = (k % 16);
              let y = parseInt(k / 16);
              pointsData.push({ value: [x * 2.4 + j * 45 + lat, y * 0.8 + lon], itemStyle: { color: Math.random() > 0.9 ? "rgb(200,0,0)" : "rgb(50,50,150)" } });
              if (Math.random() > 0.95) {
                computes.push([x * 2.4 + j * 45 + lat, y * 0.8 + lon]);
              }
            }
          }
          lon -= 5.2;
        }
        let ios = [];
        // IO转发节点
        lat = -180;
        lon = -10;
        for (let i = 0; i < 96; i++) {
          let k = parseInt(i / 12);
          pointsData.push([i * 2 + lat + k * 21 + 7, lon]);
          if (Math.random() > 0.8) {
            ios.push([i * 2 + lat + k * 21 + 7, lon]);
          }
        }
        let storages = [];
        // 存储节点
        lat = -180;
        lon = -25;
        for (let i = 0; i < 24; i++) {
          let k = parseInt(i / 4);
          pointsData.push([i * 2 + lat + k * 37 + 15, lon]);
          if (Math.random() > 0.8) {
            storages.push([i * 2 + lat + k * 37 + 15, lon]);
          }
        }

        let series = [];
        let lines = [];
        for (let i = 0; i < computes.length; i++) {
          //选择一个io转发节点
          let index = parseInt(Math.random() * ios.length);
          lines.push([[computes[i][0], computes[i][1]], [ios[index][0], ios[index][1]]]);
        }
        for (let i = 0; i < ios.length; i++) {
          //选择一个存储发节点
          let index = parseInt(Math.random() * storages.length);
          lines.push([[ios[i][0], ios[i][1]], [storages[index][0], storages[index][1]]]);
        }

        series.push({
          type: 'lines3D',
          name: 'xxx',
          effect: {
            show: true,
            trailWidth: 3,
            trailLength: 0.12,
            trailOpacity: 1,
            trailColor: 'rgb(30, 30, 60)'
          },
          lineStyle: {
            width: 1,
            color: 'rgb(50, 50, 150)',
            // color: 'rgb(118, 233, 241)',
            opacity: 0.1
          },
          blendMode: 'lighter',
          data: lines,
        })
        // ~3万个点
        series.push({
          type: 'scatter3D',
          coordinateSystem: 'globe',
          blendMode: 'lighter',
          Symbol: "roundRect",
          symbolSize: 3,
          itemStyle: {
            color: 'rgb(50, 50, 150)',
            opacity: 1
          },
          data: pointsData
        })


        myChart.setOption({
          legend: {
            selectedMode: 'single',
            left: 'left',
            data: Object.keys(routesGroupByAirline),
            orient: 'vertical',
            textStyle: {
              color: '#fff'
            }
          },
          globe: {
            environment: ROOT_PATH + '/data-gl/asset/background.jpg',
            displacementScale: 0.1,
            displacementQuality: 'high',
            baseColor: '#000',
            shading: 'realistic',
            realisticMaterial: {
              roughness: 0.2,
              metalness: 0
            },
            postEffect: {
              enable: true,
              depthOfField: {
                enable: false,
                focalDistance: 150
              }
            },
            temporalSuperSampling: {
              enable: true
            },
            light: {
              ambient: {
                intensity: 0
              },
              main: {
                intensity: 0.1,
                shadow: false
              },
              ambientCubemap: {
                texture: ROOT_PATH + '/data-gl/asset/lake.hdr',
                exposure: 1,
                diffuseIntensity: 0.5,
                specularIntensity: 2
              }
            },
            viewControl: {
              // autoRotate: true,
              autoRotateSpeed: 1,
              alpha: 20 ,//初始角度
              distance: 250
            },
            silent: true
          },
          series: series
        });


        window.addEventListener('keydown', function () {
          series.forEach(function (series, idx) {
            myChart.dispatchAction({
              type: 'lines3DToggleEffect',
              seriesIndex: idx
            });
          });
        });
      });
      if (option && typeof option === 'object') {
        myChart.setOption(option);
      }
    }
  }
}
</script>
