<template>
  <div id="tttt"></div>
</template>

<script>
var echarts = require("echarts");
import BcChartItem from "./BcChartItem.vue";
export default {
  name: "BottomCharts",
  components: {
    BcChartItem,
  },
  methods: {
    draw() {
      let vue = this
      let myChartRightCenter = echarts.init(
					document.getElementById("tttt")
				);
				let option = {
					grid: {
            top: '18%',
						left: '3%',
						right: '4%',
						bottom: '10%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						boundaryGap: false,
						data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
						axisLabel: {
							textStyle: {
								color:'rgba(255, 255, 255, 0.6)'
							},
						},
					}],
					yAxis: [{
            name: '(%)         ',
						type: 'value',
						axisLabel: {
							textStyle: {
								color:'rgba(255, 255, 255, 0.4)'
							},
							formatter: '{value}'
						},
						axisLine: {
							show: false,
							symbol: [''],
							symbolOffset: [0, 10],
						},
            splitLine: {
              lineStyle: {
                type: 'dashed',
                color:'rgba(255, 255, 255, 0.4)'
              }
            }
					}],
					series: [{
							type: 'line',
							data: this.$store.state.BottomFloatData,
							color: '#6fcffb',
							smooth: true
						}
					]
				};
				myChartRightCenter.setOption(option);
				window.addEventListener("resize", () => myChartRightCenter.resize(), false);
    }
  },
  mounted() {
    this.draw();
  },
};
</script>

<style lang="less">
#tttt {
  width: 100%;
  height: 100%;
}
</style>
