<template>
  <div id="right-chart-2">
    <div class="header-2">资源近7天使用情况变化</div>
    <div id="body-2"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data() {},
  mounted() {
    // let body_2 = document.getElementById('body-2')
    var chartDom = document.getElementById("body-2");
    // console.log(body_2.offsetHeight)
    // console.log(body_2.offsetWidth)
    // chartDom.style.width = `${body_2.offsetWidth}px`
    // chartDom.style.height = `${body_2.offsetHeight}px`
    var myChart = echarts.init(chartDom);
    var option;

    option = {
      grid: {
        top: "21%",
        bottom: "14%",
        left: "15%",
        right: "6%", //调整这里,可能可以让x轴横坐标全部显示
      },
      yAxis: {
        type: "value",
        name: "排队时间(H)", //坐标轴名称
        axisLabel: {
          //y轴文字的配置
          show: true,
          /*textStyle: {
            color: "rgba(219,225,255,1)",
          },*/
        },
        axisLine: {
          show: false, // 显示坐标轴线
        },
        axisTick: {
          show: false, // 显示坐标轴刻度线
        },
        splitLine: {
          show: true,
          lineStyle: {
            type: "dashed",
            color: "rgba(255, 255, 255, 0.4)",
          },
        },
      },
      xAxis: {
        /* show: true,
        splitLine: {
          show: true,
        },
        */
        type: "category",
        data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
        axisLine: {
          show: true,
          // symbol: ["none", "arrow"], //箭头一端没效果,一端箭头
          //symbolOffset: [0, 9], //箭头段移动8个像素
        },
        axisTick: {
          show: false,
        },//去除横坐标的刻度线
      },
      series: [
        {
          data: this.$store.state.RightChart2Data,
          // barWidth: '100%',
          // barCategoryGap: '500px',
          type: "bar",
          barWidth: 14,
          //showBackground: true,
          /*  backgroundStyle: {
            color: "rgba(180, 180, 180, 0.2)",
          },
*/
          itemStyle: {
            // barBorderRadius: [2, 2, 0, 0], //柱体圆角
            color: new echarts.graphic.LinearGradient(
              //前四个参数用于配置渐变色的起止位置，这四个参数依次对应 右下左上 四个方位。也就是从右边开始顺时针方向。
              //通过修改前4个参数，可以实现不同的渐变方向
              /*第五个参数则是一个数组，用于配置颜色的渐变过程。
                          每项为一个对象，包含offset和color两个参数
                        */
              0,
              1,
              0,
              0,
              [
                {
                  //代表渐变色从正上方开始
                  offset: 0, //offset范围是0~1，用于表示位置，0是指0%处的颜色
                  color: "#3AD0FF",
                }, //柱图渐变色
                {
                  offset: 0.43, //offset范围是0~1，用于表示位置，0是指0%处的颜色
                  color: "#3AD0FF",
                }, //柱图渐变色

                {
                  offset: 1, //指100%处的颜色
                  color: "#be78ea",
                },
              ]
            ),

            /* normal: {//右边显示柱状图的数值
								label: {
									show: true, //开启显示
									position: 'right', //在上方显示
									textStyle: { //数值样式
										color: 'white',
										fontSize: 14
									}
								}
							} */
          },
        },
      ],
    };
    option && myChart.setOption(option);
  },
};
</script>

<style lang="less">
#right-chart-2 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("./img/output/right02.png");
  background-size: 100% 100%;
}
.header-2 {
  width: 100%;

  font-size: 22px;
  flex: 0.3;
  display: flex;
  padding: 1% 0% 0% 10%;
}
#body-2 {
  flex: 1.5;
}
</style>
