<template>
  <div id="bC-chart-item">
    <div id="head-5">资源年均利用率</div>
    <div id="body-5">
      
      <div id="container1">
        <span class="text1">CPU利用率</span>

      </div>

      
      <div id="container2" >
        <span class="text2">Net利用率</span>
      </div>

    </div>
  </div>
</template>
<script>
import { Liquid, measureTextWidth } from "@antv/g2plot";
export default {
  data() {
    return {};
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      const liquidPlot = new Liquid(
        document.getElementById("container1"),
        {
          percent: 0.45,//容量大小
          shape: "rect",
          
          outline: {
            border: 2,
            distance: 0,
          },
          wave: {
            length: 128,
          },
           statistic:{
            content:{
              style:({percent})=>({
                fontSize:22,
                
               // fill:percent>0.5?'white':'red',指数字的颜色,可以按照容量多少来定义
              }),
              formatter:(percent)=>{
              console.log(percent.percent)
              return `${(percent.percent*100).toFixed(0)}%`
            }//去除小数点后两位不要的0

            }
          },
          color: () => '#3AD0FF',
          
          
        }
      );
      const liquidPlot2 = new Liquid(
        document.getElementById("container2"),
        {
         percent: 0.45,
          shape: "rect",
          outline: {
            border: 2,
            distance: 0,
          },
          wave: {
            length:128,
          },
          statistic:{
            content:{
              style:({percent})=>({
                fontSize:22
              }),
              formatter:(percent)=>{
              console.log(percent.percent)
              return `${(percent.percent*100).toFixed(0)}%`
            }//去除小数点后两位不要的0

            }
          },
        color: () => '#3AD0FF',
        
        }
      );
      liquidPlot.render();
      liquidPlot2.render();
    },
  },
};
</script> 
<style scoped>
#bC-chart-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("./img/output/right04.png");
  /* 背景图 */
  background-size: 100% 100%;
}
#head-5 {
  font-size: 22px;
  height: 20%;
  width: 100%;
  display: flex;
  padding: 0% 0% 0% 9%;
}
#body-5 {
  width: 280px;
  height: 180px;
  display: flex;
  position: absolute;
  padding: 3% 0% 2% 0%;
}
 .text1{
  font-size: 13px;
  padding: 0% 0% 0% 26%;
  color:white
}
.text2{
  font-size: 13px;
  padding: 0% 0% 0% 27%;
  color:white
} 
#container1{
  width:150px;
  height:100px;
  opacity: 0.83;
  
}
#container2{
  width:150px;
  height:100px;
  opacity: 0.83;
}

</style>
