<template>
  <div class="bottom-charts">
    <div class="bc-chart-item">
      <div id="bc-chart-item-left"></div>
    </div>

    <dv-decoration-2 class="decoration-2" :reverse="true" style="width:5px;" />

    <div class="bc-chart-item">

    </div>

    <dv-decoration-2 class="decoration-3" :reverse="true" style="width:5px;" />


    <div class="bc-chart-item">
		<!-- 右边部分-->
		<BcChartItem />
    </div>

  </div>
</template>

<script>
import * as echarts from 'echarts';
import BcChartItem from "./BcChartItem.vue";
export default {
  name: 'BottomCharts',
  components: {
    BcChartItem,
  },
  methods: {
    drawLeft() {
      let myChartRightCenter = echarts.init(
					document.getElementById("bc-chart-item-left")
				);
				let option = {
					title: {
						text: '近24小时使用情况',
						left: 'left',
						textStyle: {
							color: 'white'
						}
					},
					legend: {
						data: ['CPU', '存储'],
						top:'3%',
						textStyle: {
							color: 'white',
							fontSize: 10,
						},
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						boundaryGap: false,
						data: ['0时', '4时', '8时', '12时', '16时', '20时'],
						axisLabel: {
							textStyle: {
								color: '#ffffff',
							},
						},
					}],
					yAxis: [{
						type: 'value',
						axisLabel: {
							textStyle: {
								color: '#ffffff',
							},
							formatter: '{value} %'
						},
						axisLine: {
							show: true,
							symbol: ['none', 'arrow'],
							symbolOffset: [0, 10],
						}
					}],
					series: [{
							name: 'CPU',
							type: 'line',
							areaStyle: {},
							data: [10,30,100,60,80,30],
							color:'#144955',
							smooth: true
						},
						{
							name: '存储',
							type: 'line',
							areaStyle: {},
							data: [30,20,40,50,70,10],
							color:'#1c5186',
							smooth: true
						}
					]
				};
				myChartRightCenter.setOption(option);
				window.addEventListener("resize", () => myChartRightCenter.resize(), false);
    }
  },
  mounted() {
    this.drawLeft();
  }
}
</script>

<style lang="less">
.bottom-charts {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;

  .bc-chart-item {
    width: 33%;
    height: 100%;
    padding-top: 0px;
    box-sizing: border-box;
  }

  .bcci-header {
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 20px;
  }

  .dv-active-ring-chart {
    height: calc(~"100% - 80px");
  }

  .label-tag {
    height: 30px;
  }

  .active-ring-name {
    font-size: 18px !important;
  }

  .decoration-1, .decoration-2, .decoration-3 {
    display: absolute;
    left: 0%;
  }
}

#bc-chart-item-left {
  width: 100%;
  height: 100%;
  border: 1px solid red;
}
</style>
